import styled from 'styled-components'

const PlainButton = styled.button`
  cursor: pointer;
  background: none;
  outline: none;
  padding: 0;
  margin: 0;
  border: 0;
  -webkit-tap-highlight-color: transparent;
`

export default PlainButton



